import React from 'react';
import PropTypes from 'prop-types';
import { Slider, ItemWrapper } from './Carousel.styles';

const Carousel = ({ ItemComponent, items, fullscreen, ...props }) => {
  const settings = {
    dots: false,
    arrows: false,
    infinite: true,
    draggable: false,
    swipe: false,
    touchMove: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    ...props,
  };
  return (
    <Slider
      fullscreen={fullscreen}
      {...settings}
      // afterChange={() => {
      //   const siblings = document.querySelectorAll('.slick-slide');
      //   const current = document.querySelector('.slick-current');

      //   for (let i = 0; i < siblings.length; i + 1) {
      //     siblings[i].style.zIndex = 0;
      //   }
      //   current.style.zIndex = 10;
      // }}
    >
      {items.map((item) => (
        <ItemWrapper key={item.id}>
          <ItemComponent item={item} />
        </ItemWrapper>
      ))}
    </Slider>
  );
};

Carousel.propTypes = {
  ItemComponent: PropTypes.element.isRequired,
  items: PropTypes.arrayOf(
    PropTypes.exact({
      id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    }),
  ),
  fullscreen: PropTypes.bool,
};

Carousel.defaultProps = {
  items: [],
  fullscreen: false,
};

export default Carousel;
