import styled from '@emotion/styled';
import { keyframes } from '@emotion/core';
import BaseContainer from 'react-bootstrap/Container';
import BaseRow from 'react-bootstrap/Row';
import BaseCol from 'react-bootstrap/Col';

const fadeInUp = keyframes`
  from {
    opacity: 0;
    visibility: hidden;
    transform: translate3d(0, 40px, 0);
  }

  to {
    visibility: visible;
    opacity: 1;
    transform: none;
  }
`;

export const Wrapper = styled('div')`
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 750px;
  position: relative;
  z-index: 0;
  flex: 1;
`;

export const Overlay = styled('div')`
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000000;
  opacity: 0;
`;

export const Container = styled(BaseContainer)`
  padding: 0 !important;
  height: 100%;
`;

export const Row = styled(BaseRow)`
  z-index: 0;
  align-items: center !important;
  justify-content: flex-end !important;
  height: 100%;

  @media screen and (min-width: 768px) {
    display: flex !important;
  }
`;

export const ColumnImage = styled(BaseCol)`
  background-image: ${(p) => (p.src ? `url(${p.src})` : 'none')};
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 50% !important;
  max-width: 50% !important;
  position: relative !important;
  height: 100%;
  z-index: -1;
`;

export const ColumnText = styled(BaseCol)`
  transform: translateZ(0px) translateY(0%);
  width: 50%;
  position: relative;
  align-items: center !important;
  display: flex !important;
  opacity: 0;
  visibility: hidden;
  animation-name: ${fadeInUp};
  animation-duration: 0.5s;
  animation-fill-mode: both;
  height: 100%;
  font-family: ${(p) => p.theme.fontFamily};

  @media screen and (min-width: 768px) {
    position: absolute !important;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 1200px !important;
    margin: 0 auto;
  }
`;

export const Text = styled('div')`
  position: relative;
  z-index: 1;
  font-family: ${(p) => p.theme.fontFamily};

  @media screen and (min-width: 992px) {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    right: 0;
    max-width: 50%;
    padding-left: 45px;
    padding-right: 3em;
  }
`;

export const Subheading = styled('span')`
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #31de79;
  background: #9ff669;
  font-family: ${(p) => p.theme.fontFamily};
  background: linear-gradient(135deg, #9ff669 0%, #31de79 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

export const Heading = styled('h1')`
  font-size: 44px !important;
  color: #000000;
  line-height: 1.3 !important;
  font-weight: 300 !important;
  margin-bottom: 1.5rem !important;
  margin-top: 1rem !important;
  font-family: ${(p) => p.theme.fontFamily};

  > span {
    font-weight: 900;
  }
`;

export const Paragraph = styled('p')`
  color: rgba(0, 0, 0, 0.8);
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  margin-top: 0;
  margin-bottom: 1rem;
  font-family: ${(p) => p.theme.fontFamily};
`;
