import React, { PureComponent } from 'react';
import { Nav } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import {
  Navbar,
  NavbarBrand,
  NavbarToggle,
  NavbarCollapse,
  NavLink,
  Container,
} from './Header.styles';

// 160

class Header extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      scrolled: false,
    };
    this.listener = this.listener.bind(this);
  }

  componentDidMount() {
    this.listener();
    window.addEventListener('scroll', this.listener);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.listener);
  }

  listener() {
    const { scrolled } = this.state;
    if (window.scrollY <= 130 && scrolled) {
      this.setState({ scrolled: false });
    }
    if (window.scrollY > 130 && !scrolled) {
      this.setState({ scrolled: true });
    }
  }

  render() {
    const { scrolled } = this.state;
    return (
      <Navbar expand="lg" scrolled={scrolled}>
        <Container>
          <NavbarBrand as={Link} to="/yukioru">
            Digi<span>Lab</span>
          </NavbarBrand>
          <NavbarToggle aria-controls="ftco-nav">
            <span className="oi oi-menu" />
            Menu
          </NavbarToggle>
          <NavbarCollapse id="ftco-nav">
            <Nav className="ml-auto">
              <Nav.Item>
                <NavLink href="#home-section">
                  <span>Home</span>
                </NavLink>
              </Nav.Item>
              <Nav.Item>
                <NavLink href="#services-section">
                  <span>Services</span>
                </NavLink>
              </Nav.Item>
              <Nav.Item>
                <NavLink href="#projects-section">
                  <span>Projects</span>
                </NavLink>
              </Nav.Item>
              <Nav.Item>
                <NavLink href="#about-section">
                  <span>About</span>
                </NavLink>
              </Nav.Item>
              <Nav.Item>
                <NavLink href="#testimony-section">
                  <span>Testimony</span>
                </NavLink>
              </Nav.Item>
              <Nav.Item>
                <NavLink href="#blog-section">
                  <span>Blog</span>
                </NavLink>
              </Nav.Item>
              <Nav.Item>
                <NavLink href="#contact-section">
                  <span>Contact</span>
                </NavLink>
              </Nav.Item>
            </Nav>
          </NavbarCollapse>
        </Container>
      </Navbar>
    );
  }
}

export default Header;
