import React from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';

import SonyaTemplate from './templates/sonya/Index';
import YukioruTemplate from './templates/yukioru/Index';
import ArgonTemplate from './templates/argon/index';

export default () => (
  <Router>
    <div>
      <Switch>
        <Route exact path="/">
          <ul>
            <li>
              <Link to="/yukioru">Yukioru Template</Link>
            </li>
            <li>
              <Link to="/sonya">Sonya Template</Link>
            </li>
            <li>
              <Link to="/argon">Argon</Link>
            </li>
          </ul>
        </Route>

        <Route path="/yukioru">
          <YukioruTemplate />
        </Route>
        <Route path="/sonya">
          <SonyaTemplate />
        </Route>
        <Route path="/argon">
          <ArgonTemplate />
        </Route>
      </Switch>
    </div>
  </Router>
);
