import React from 'react';
import CountUp from 'react-countup';
import * as Styles from './IconWIthTitle.styles';

// eslint-disable-next-line react/prop-types
export default ({
  num,
  text,
  icon,
  duration,
  numDelay,
  preserveValue,
  redraw,
}) => (
  <Styles.IconWithTitle>
    <Styles.IconWrapper>{icon}</Styles.IconWrapper>
    <Styles.TitleNumWrapper>
      <CountUp
        end={num}
        duration={duration}
        suffix="+"
        delay={numDelay}
        preserveValue={preserveValue}
        redraw={redraw}
      />
    </Styles.TitleNumWrapper>
    <Styles.Text>{text}</Styles.Text>
  </Styles.IconWithTitle>
);
