import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { withRouter } from 'react-router';
// reactstrap components
import { Container, Row, Col } from 'reactstrap';
// core components
import routes from '../routes';

import AdminFooter from '../components/AdminFooter';
import Sidebar from '../components/Sidebar';
import Logo from '../assets/img/brand/argon-react.png';

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.mainContent = React.createRef();
  }

  componentDidUpdate() {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    if (this.mainContent.current) {
      this.mainContent.current.scrollTop = 0;
    }
  }

  getRoutes = (routes) => {
    return routes.map((prop, key) => {
      const Component = prop.component;
      return (
        <Route exact={prop.exact} path={prop.layout + prop.path} key={key}>
          {prop.redirect && !Component ? (
            <Redirect to={prop.redirect} />
          ) : (
            <Component />
          )}
        </Route>
      );
    });
  };

  render() {
    return (
      <>
        <Sidebar
          {...this.props}
          routes={routes}
          logo={{
            innerLink: '/argon',
            imgSrc: Logo,
            imgAlt: '...',
          }}
        />
        <div className="main-content" ref={this.mainContent}>
          <Container>
            <Row>
              <Col
                className="mb-5 mb-xl-0"
                xl={{ size: 5, offset: 3 }}
                style={{ paddingTop: 100 }}
              >
                <Switch>{this.getRoutes(routes)}</Switch>
              </Col>
            </Row>
          </Container>
          <Container fluid>
            <AdminFooter />
          </Container>
        </div>
      </>
    );
  }
}

export default withRouter(Main);
