import styled from '@emotion/styled';
import { css } from '@emotion/core';
import BaseButton from 'react-bootstrap/Button';

export default styled(BaseButton)`
  font-family: ${(props) => props.theme.fontFamily} !important;
  ${(props) => {
    if (props.variant === 'outline-primary') {
      return css`
        background: transparent !important;
        border-color: #f39c12 !important;
        color: #f39c12 !important;
        padding-top: 10px !important;
        padding-bottom: 10px !important;
        padding-left: 30px !important;
        padding-right: 30px !important;
        font-size: 19px !important;
        text-transform: uppercase;
        border-radius: 3px !important;
        transition: all 1s !important;
        &:hover {
          background-color: #f39c12 !important;
          color: ${props.theme.colors.white} !important;
        }
        &:focus {
          box-shadow: none !important;
        }
      `;
    }
    if (props.variant === 'primary') {
      return css`
        background: #f39c12 !important;
        border: 1px solid #f39c12 !important;
        color: ${props.theme.colors.white} !important;
        border-radius: 3px !important;
        padding: 15px 35px !important;
        text-transform: uppercase;
        transition: all 1s !important;
        &:hover {
          background: #ff6b80 !important;
          border-color: #ff6b80 !important;
        }
      `;
    }
    if (props.variant === 'light') {
      return css`
        color: #a9adb0 !important;
        margin-left: 20px;
        text-transform: uppercase;
        font-size: 19px !important;
        font-weight: 600 !important;
        background: transparent !important;
        transition: all 1s !important;
        border: none !important;
        &:hover {
          color: ${props.theme.colors.white} !important;
        }
      `;
    }
    if (props.variant === 'outline-secondary');
    return css`
      background: transparent !important;
      border: 1px solid #929292 !important;
      color: #686868 !important;
      border-radius: 3px !important;
      padding: 12px 35px !important;
      text-transform: uppercase !important;
      -webkit-transition: all 1s !important;
      -moz-transition: all 1s !important;
      -o-transition: all 1s !important;
      transition: all 1s;
      font-size: 14px !important;
      &:hover {
        background: #f39c12 !important;
        color: #fff !important;
        border: 1px solid #f39c12 !important;
      }
    `;
  }}
`;
