import styled from '@emotion/styled';
import { css } from '@emotion/core';
import BaseContainer from 'react-bootstrap/Container';
import BaseNavbar from 'react-bootstrap/Navbar';
import BaseNav from 'react-bootstrap/Nav';

export const Container = styled(BaseContainer)``;

export const NavbarBrand = styled(BaseNavbar.Brand)`
  padding-top: 0.7rem !important;
  padding-bottom: 0.7rem !important;
  font-weight: 900 !important;
  font-size: 24px !important;
  text-transform: uppercase;
  color: #000 !important;
  @media screen and (max-width: 991.98px) {
    color: #fff !important;
  }
  > span {
    color: #31de79;
    background: #9ff669;
    background: linear-gradient(135deg, #9ff669 0%, #31de79 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
`;

export const NavbarToggle = styled(BaseNavbar.Toggle)`
  border: none !important;
  color: rgba(255, 255, 255, 0.5) !important;
  cursor: pointer !important;
  padding-right: 0 !important;
  text-transform: uppercase;
  font-size: 16px !important;
  letter-spacing: 0.1em;
  font-family: ${(p) => p.theme.fontFamily};
  font-weight: 600;
  outline: none !important;

  > span {
    padding-right: 4px;
  }
`;

export const NavbarCollapse = styled(BaseNavbar.Collapse)``;

export const NavLink = styled(BaseNav.Link)`
  font-size: 16px;
  padding-top: 0.7rem !important;
  padding-bottom: 0.7rem !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
  color: #000000 !important;
  font-weight: 400;
  opacity: 1 !important;

  @media screen and (max-width: 991.98px) {
    padding-left: 0 !important;
    padding-right: 0 !important;
    padding-bottom: 1rem !important;
    padding-top: 0 !important;
    color: #fff !important;
  }

  &.active {
    background: none !important;
    color: #31de79 !important;

    > span:before {
      visibility: visible;
      transform: scaleX(1);
    }
  }

  > span {
    position: relative;
    display: block;
    padding-bottom: 2px;

    @media screen and (max-width: 1199.98px) {
      display: inline-block;
    }

    &:before {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0;
      left: 0;
      background: #31de79;
      visibility: visible;
      transform: scaleX(0);
      transition: all 0.3s ease-in-out 0s;
    }
  }
`;

const scrolledStyle = css`
  position: fixed !important;
  margin-top: 0px !important;
  background: #fff !important;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  padding: 0 15px !important;
`;

const shouldForwardProp = (prop) => !['scrolled', 'standBy'].includes(prop);
export const Navbar = styled(BaseNavbar, { shouldForwardProp })`
  background: transparent !important;
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  min-height: 65px;

  ${Container} {
    width: 1110px;
    padding: 0;
  }

  @media screen and (max-width: 991.98px) {
    background: #000000 !important;
    position: relative;
    top: 0;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    padding-left: 15px !important;
    padding-right: 15px !important;
  }

  margin-top: ${(p) => (!p.scrolled ? 0 : '-130px')} !important;
  transition: all 0.3s ease-out;

  ${(p) => p.scrolled && scrolledStyle}
`;
