import styled from '@emotion/styled';
import { css } from '@emotion/core';
import BaseButton from 'react-bootstrap/Button';

const variants = {
  primary: css`
    background: #9ff669 !important;
    background: linear-gradient(135deg, #9ff669 0%, #31de79 100%) !important;
    border: 1px solid #31de79 !important;
    color: #fff !important;
    &:hover {
      border: 1px solid #31de79 !important;
      background: #9ff669 !important;
      background: linear-gradient(135deg, #9ff669 0%, #31de79 100%) !important;
    }
  `,
  default: '',
};

function makeVariant(p) {
  const isValid = Object.keys(variants).includes(p.variant);
  return variants[isValid ? p.variant : 'default'];
}

export default styled(BaseButton)`
  border-radius: 30px !important;
  box-shadow: 0px 24px 36px -11px rgba(0, 0, 0, 0.09) !important;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out !important;
  font-family: ${(p) => p.theme.fontFamily} !important;
  text-transform: inherit !important;
  font-weight: 600 !important;
  outline: none !important;

  &:hover {
    outline: none !important;
  }

  &:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5) !important;
  }

  ${makeVariant};
`;
